import React, {Component} from "react";
import PropTypes from 'prop-types';
import MultiCartItemComponent from "../MultiCartItemComponent";
import {updateCartDevices, showErrorMessage, setFilters} from "eshop/modules/cart/actions/cart";
import {connect} from "react-redux";
import {getBaseProductIdForBundle} from "../../../../selectors";
import { getMarketContext } from "eshop/modules/configurator/selectors/filters"
import {lowerInstallmentsModalOpen} from "eshop/modules/cart/actions/resources";

class TerminalEntriesListComponent extends Component {

    constructor(props){
        super(props);
    }

    render(){
        return (
            <div>
                {this.props.entries && this.props.entries.map((terminal, terminalIndex) =>  {
                    return <WrappedTerminalEntryComponent removeModalTexts={this.props.removeModalTexts}
                    key={"TERMINAL" + terminalIndex + "_" + this.props.idx}
                    entry={terminal}
                    cartBundle={this.props.cartBundle}
                    bundle={this.props.bundle}
                    propositionId={this.props.propositionId}
                    sapReservation={this.props.sapReservation}
                    sapErrorMessage={this.props.sapErrorMessage}
                    descriptions={this.props.descriptions}
                    lowerInstallmentsActive={this.props.lowerInstallmentsActive}
                    editable={this.props.editable}/>
                })}

            </div>
        );
    }
}

TerminalEntriesListComponent.propTypes = {
    idx: PropTypes.number.isRequired,
    entries: PropTypes.arrayOf(PropTypes.object),
    detailsHeader: PropTypes.string,
    editable:PropTypes.bool
}
TerminalEntriesListComponent.defaultProps = {
   editable:true
};
class TerminalEntryComponent extends Component {

    constructor(props){
        super(props);
    }

    onRemoveClicked(){
        if (this.props.sapReservation !== '') {
            this.props.showErrorMessage(this.props.sapErrorMessage);
        } else {
            console.log("Terminal delete clicked");
            let toBeRemoved = [this.props.entry.productCode];
            this.props.updateCartDevices(this.props.propositionId,this.props.bundle,this.props.cartBundle,toBeRemoved,[]);
        }
    }

    onChangeClicked(){
        console.log("Terminal change clicked");
        this.props.setFilters(this.props.cartBundle)
        if (this.props.baseProductId) {
             let returnLink=this.props.isB2B?"/male-firmy/sklep":"/sklep";
             window.location = returnLink+"?serviceplan=" + this.props.baseProductId + "&propositionId=" + this.props.propositionId;
        }
    }

    lowerInstallmentsClicked(bundleNo){
        this.props.lowerInstallmentsModalOpen(bundleNo);
    }

    render(){
    console.log(this.props);
        return (<MultiCartItemComponent
                    removeModalTexts={this.props.editable&&this.props.removeModalTexts}
                    detailsHeader={this.props.editable&&this.props.detailsHeader}
                    entry={this.props.entry}
                    startPricePropertyName="checkoutPrice"
                    type="TERMINAL"
                    topBorder={true}
                    onRemoveClicked={this.props.editable&&this.onRemoveClicked.bind(this)}
                    onChangeClicked={this.props.editable&&this.onChangeClicked.bind(this)}
                    descriptions={this.props.descriptions}
                    lowerInstallmentsClicked={!!this.props.lowerInstallmentsActive ? this.lowerInstallmentsClicked.bind(this, this.props.entry.bundleNo) : undefined}
                    />);
    }
}

const mapStateToProps = () => {
    const getBaseProductId = getBaseProductIdForBundle();

    return (state, props) => ({
        baseProductId: getBaseProductId(state, props.cartBundle),
        isB2B: getMarketContext(state)==='B2B'?true:false
    });
};

const mapDispatchToProps = (dispatch) => ({
    updateCartDevices: (bundle, offer, cartBundle, productsToRemove, productsToAdd) => dispatch(updateCartDevices(bundle, offer, cartBundle, productsToRemove, productsToAdd)),
    showErrorMessage: (message) => dispatch(showErrorMessage(message)),
    lowerInstallmentsModalOpen: (bundleNo) => dispatch(lowerInstallmentsModalOpen(bundleNo)),
    setFilters: (bundleNo) => dispatch(setFilters(bundleNo))
});

const WrappedTerminalEntryComponent =  connect(mapStateToProps,mapDispatchToProps)(TerminalEntryComponent);

export default TerminalEntriesListComponent;